.user_menu_container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(2, 136, 239);
  border-radius: 5px;
  height: 750px;
  width: 250px;
  transition: 500ms;
}
.user_menu_inactive {
  width: 0px;
  transition: 500ms;
}

.li {
  color: white;
  font-size: 18px;
  font-weight: 500;
}

@media screen and (max-width: 768px) {
  .user_menu_container {
    margin-right: 5px;

  }
}
