.container {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(1, 120, 231, 0.727);
}
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  color: white;
}
.loading_tag{
  background-color: rgba(255, 255, 255, 0.72);
  height: 160px;
  width: 350px;
  color: rgb(1, 142, 202);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  font-size: 25px;
}
.icon {
  font-size: 30px;
  padding-top: 10px;
  margin-left: 10px;

  animation: spin infinite 2s linear;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
