.container {
  display: flex;
  justify-content: space-between;
}

.middle_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 20px;
  justify-content: space-around;
  color: white;
  background-color: rgba(4, 141, 221, 0.794);
  width: 30%;
  height: 450px;
  border-radius: 10px;
}

.right_bar {
  display: flex;
  background-color: rgb(2, 136, 239);
  min-height: 90vh;
  width: 50px;
  border-radius: 5px;
}

#welcome_message {
  color: white;
}

@media screen and (max-width: 768px) {
  .container {
    min-height: 60vh;
  }
  .right_bar {
    display: none;
  }

  .middle_container {
    width: 100%;
  }
  .middle_current_container {
    width: 100%;
  }
}
