.container {
  position: fixed;
  height: 150%;
  width: 110%;
  background-color: rgba(0, 0, 0, 0.703);
  display: flex;
  justify-content: center;
  align-items: center;
}
.action {
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.exit {
  padding-left: 25px;
  padding-top: 10px;
margin-bottom: 15px;
  color: black;
}
.container_action{
  height: 200px;
  width: 40%;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.81);
}
@media (max-width: 768px) {
  .container_action {
    width: 100%;
  }

}
